<template>
  <div class="wrapper">
    <v-head></v-head>
    <v-sidebar></v-sidebar>
    <div class="content">
      <transition name="move" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>
<script>
  import vHead from './Header.vue'
  import vSidebar from './Sidebar.vue'

  export default {
    components: {
      vHead, vSidebar
    }
  }
</script>
<style>
    .content {
        background: none repeat scroll 0 0 #fff;
        position: absolute;
        left: 250px;
        right: 0;
        top: 70px;
        bottom:0;
        width: auto;
        padding:40px;
        box-sizing: border-box;
        overflow-y: scroll;
    }
    .crumbs{
        margin-bottom: 20px;
    }
    .pagination{
        margin: 20px 0;
        text-align: right;
    }
    .plugins-tips{
        padding:20px 10px;
        margin-bottom: 20px;
    }
    .el-button+.el-tooltip {
        margin-left: 10px;
    }

    .el-table tr:hover{
        background: #f6faff;
    }
    .mgb20{
        margin-bottom: 20px;
    }

    .move-enter-active,.move-leave-active{
        transition: opacity .5s;
    }
    .move-enter,.move-leave{
        opacity: 0;
    }
    /*BaseForm*/
    .form-box{
        width:600px;
    }
    .form-box .line{
        text-align: center;
    }
    .el-time-panel__content::after, .el-time-panel__content::before {
        margin-top: -7px;
    }
    /*Readme*/
    .ms-doc .el-checkbox__input.is-disabled+.el-checkbox__label{
        color: #333;
        cursor: pointer;
    }
    /*VueEditor*/
    .ql-container{
        min-height: 400px;
    }
    .ql-snow .ql-tooltip{
        transform: translateX(117.5px) translateY(10px) !important;
    }
    .editor-btn{
        margin-top: 20px;
    }
</style>



